import React from 'react';
import { NavLink } from 'react-router-dom'

const Navbar = () => {
    return <>
        <ul className="nav navbar">
            <li><NavLink>Category</NavLink></li>
            <li><NavLink>Posts</NavLink></li>
            <li><NavLink>About</NavLink></li>
        </ul>
    </>
}
export default Navbar